
@import '~styles/base';

.level51-ajaxMultiSelectField-itemHeader {
  a {
    display: flex;
    align-items: center;
    color: @color-textBase;
  }

  span {
    display: flex;
    margin-left: @space-1;
  }

  &.level51-ajaxMultiSelectField-itemHeader--active {
    a {
      color: @color-highlight;
    }
  }
}
